* {
    box-sizing: border-box;
  }
  
:root {
    --color-bg: #2C2C32;
    --color-active: #FF9999;
}

body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

header {
    background-color: var(--color-bg);
    padding: 2rem 0;
    text-align: center;
}
    header > a {
        color: white;
        text-decoration: none;
        padding: 0 1rem;
    }
    header > a:hover {
        text-decoration: underline;
    }
  
h1, h2, h3 {
    text-align: center;
    margin-top: 0;
}

.container {
    max-width: 900px;
    padding-left: 2rem;
    padding-right: 2rem;
    margin: 0 auto;
}

main {
    min-height: calc(100vh - 85px - 22px);
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}
    main a {
        text-decoration: none;
        color: var(--color-bg);
    }
    main a:hover {
        text-decoration: underline;
    }

.active {
    color: var(--color-active);
    cursor: default;
}

.current {
    color: var(--color-active);
    cursor: default;
}